import Avatar from "@components/avatar"
import AvatarGroup from "@components/avatar-group"
import UILoader from "@components/ui-loader"
import illustration from "@src/assets/images/illustration/email.svg"
import { Loader } from "@src/shared/CommonComponents"
import { getDataList } from "@src/shared/dynamic_pages/store"
import "moment-timezone"
import "moment/locale/id"
import { useEffect } from "react"
import { Calendar, MapPin, Video } from "react-feather"
import { useTranslation } from "react-i18next"
import Moment from "react-moment"
import { useDispatch, useSelector } from "react-redux"
import {
  Card,
  CardBody,
  CardHeader,
  CardText,
  CardTitle,
  Col
} from "reactstrap"

import EventAvailableOutlinedIcon from "@mui/icons-material/EventAvailableOutlined"
import { Swiper, SwiperSlide } from "swiper/react/swiper-react"
const params = {
  // navigation: true
  pagination: {
    clickable: true
  },
  autoplay: {
    delay: 7500,
    disableOnInteraction: false
  }
}

const WidgetTrainingTask = ({ hideCardTitle = false, id_user = null }) => {
  const { t } = useTranslation()
  // const store = useSelector((state) => state[storeName])
  const store = useSelector((state) => state.dynamicStore)
  const dynamicTempState = useSelector((state) => state.dynamic_temp_state)
  const dispatch = useDispatch()

  const storeName = "TrainingEmployee_task"
  const endpointName = "TrainingEmployee/assignment"

  useEffect(() => {
    if (
      !store.pageData[storeName] ||
      (store.pageData[storeName]?.data?.length === 0 &&
        !store.pageData[storeName]?.isLoading)
    ) {
      dispatch(
        getDataList({
          storeName: storeName,
          endpointName: endpointName,
          baseUrl: "hr",
          type: "to_me",
          Id_AttendingState: 1
        })
      )
    }
  }, [])

  const calendarStrings = {
    lastDay: "[Yesterday]",
    sameDay: "[Today]",
    nextDay: "[Tomorrow]",
    lastWeek: "[last] dddd",
    nextWeek: "dddd",
    sameElse: "L"
  }

  const toUpperCaseFilter = (d) => {
    return d.toUpperCase()
  }

  const renderAvatarGroup = (data) => {
    if (data) {
      const minCount = 10
      const avatarData = data.map((item, i) => {
        let name = `${item?.FirstName} ${item?.LastName}`
        if (i <= minCount) {
          return {
            title: `${name.replaceAll(".", "")}`,
            placement: "bottom",
            img:
              item?.PhotoPath ??
              require("@src/assets/images/avatars/avatar-blank.png").default,
            imgHeight: 33,
            imgWidth: 33
          }
        }
      })
      const remainCount = (data.length ?? 0) - minCount
      if (remainCount > 0 && avatarData) {
        avatarData.push({
          meta: `+${remainCount}`
        })
      } else {
        avatarData.push({
          meta: `${avatarData.length} ${t("Employee ready to attend")}`
        })
      }
      return <AvatarGroup data={avatarData} />
    } else {
      return null
    }
  }

  {
    return store.pageData[storeName]?.isLoading ? (
      <Col lg="12" xs="12">
        <Card style={{ minHeight: "200px" }}>
          <CardHeader>
            <div className="d-flex align-items-center">
              <EventAvailableOutlinedIcon className="user-timeline-title-icon me-1" />
              <CardTitle tag="h4">{t("Training Task")}</CardTitle>
            </div>
          </CardHeader>
          <UILoader blocking={true} loader={<Loader />}>
            <CardBody></CardBody>
          </UILoader>
        </Card>
      </Col>
    ) : store.pageData[storeName]?.data?.length > 0 ? (
      <Col lg="12" xs="12">
        <Card>
          <CardHeader>
            <div className="d-flex align-items-center">
              <EventAvailableOutlinedIcon className="user-timeline-title-icon me-1" />
              {!store.pageData[storeName]?.data[0]?.isManager ? (
                <CardTitle tag="h4">{t("My Training Task")}</CardTitle>
              ) : (
                <CardTitle tag="h4">{t("Me & Staff Training Task")}</CardTitle>
              )}
            </div>
          </CardHeader>
          <CardBody className="px-0 pb-0 ">
            <Swiper {...params}>
              {store.pageData[storeName]?.data.map((row) => {
                return (
                  <SwiperSlide key={`swiper_${row.Id_Training}`}>
                    <Card className="card-developer-meetup  cursor-pointer">
                      <div className="meetup-img-wrapper rounded-top text-center">
                        <img
                          src={
                            row?.TrainingData?.Image
                              ? row.TrainingData.Image
                              : illustration
                          }
                          height="170"
                        />
                      </div>
                      <CardBody>
                        <div className="meetup-header d-flex align-items-center">
                          <div className="meetup-day">
                            <h6 className="mb-0">
                              <Moment filter={toUpperCaseFilter} format="ddd">
                                {row?.TrainingData?.StartDate}
                              </Moment>
                            </h6>
                            <h3 className="mb-0">
                              <Moment format="DD">
                                {row?.TrainingData?.StartDate}
                              </Moment>
                            </h3>
                          </div>
                          <div className="my-auto text-truncate">
                            <CardTitle tag="h4" className="mb-25">
                              {row?.TrainingData?.Name}
                            </CardTitle>
                            <CardText className="mb-0">
                              {row?.TrainingData?.Description}
                            </CardText>
                          </div>
                        </div>
                        <div className="d-flex">
                          <Avatar
                            color="light-primary"
                            className="rounded me-1"
                            icon={<Calendar size={18} />}
                          />
                          <div>
                            {/* <h6 className="mb-0">Sat, May 25, 2020</h6>
                                <small>10:AM to 6:PM</small> */}
                            <h6 className="mb-0">
                              {
                                <Moment calendar={calendarStrings}>
                                  {row?.TrainingData?.StartDate}
                                </Moment>
                              }
                            </h6>
                            <small>
                              {
                                <Moment format="[Start at] LT">
                                  {row?.TrainingData?.StartDate}
                                </Moment>
                              }
                            </small>
                          </div>
                        </div>
                        {!row?.TrainingData?.IsOnline ? (
                          <div className="d-flex mt-2">
                            <Avatar
                              color="light-primary"
                              className="rounded me-1"
                              icon={<MapPin size={18} />}
                            />
                            <div>
                              {/* <h6 className="mb-0">Central Park</h6> */}
                              <small>{row?.TrainingData?.Address}</small>
                            </div>
                          </div>
                        ) : (
                          <div className="d-flex mt-2">
                            <Avatar
                              color="light-primary"
                              className="rounded me-1"
                              icon={<Video size={18} />}
                            />
                            <div>
                              <h6 className="mb-0">{t("Online Training")}</h6>
                              <small>
                                {row?.TrainingData?.LinkRef
                                  ? row?.TrainingData?.LinkRef
                                  : "The link will be sent before the event starts"}
                              </small>
                            </div>
                          </div>
                        )}
                        {renderAvatarGroup(row?.Employee)}
                      </CardBody>
                    </Card>
                  </SwiperSlide>
                )
              })}
            </Swiper>
          </CardBody>
        </Card>
      </Col>
    ) : null
  }
}

export default WidgetTrainingTask
